(function() {
  'use strict';

  angular
    .module('dowser')
    .controller('AgreementController', AgreementController);

  /** @ngInject */
  function AgreementController($rootScope, $state, $timeout, $stateParams, $log, Api) {
    var vm = this;

    vm.accept = debounce(function() {
      Api.post('DataType/Insert', $stateParams.data)
        .then(function(response) {
          $rootScope.$emit('wizard:progress');
          $state.go('dashboard');
          $log.debug(response);
        })
        .catch($log.error);
    });

    vm.deny = debounce(function() {
      $rootScope.$emit('wizard:progress');
      $state.go('main');
    });

    function debounce(callback) {
      return function() {
        $rootScope.$emit('wizard:progress');
        $timeout(callback, 500);
      };
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('dowser')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('main', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('wizard', {
        url: '/wizard',
        templateUrl: 'app/wizard/wizard.html',
        controller: 'WizardController',
        controllerAs: 'wizard'
      })
      .state('wizard.register', {
        templateUrl: 'app/register/register.html',
        controller: 'RegisterController',
        controllerAs: 'register'
      })
      .state('wizard.importer', {
        params: {name: undefined, kind: undefined, userId: 'user'},
        templateUrl: 'app/importer/importer.html',
        controller: 'ImporterController',
        controllerAs: 'importer'
      })
      .state('wizard.agreement', {
        params: {data: {}},
        templateUrl: 'app/agreement/agreement.html',
        controller: 'AgreementController',
        controllerAs: 'agreement'
      })
      .state('dashboard', {
        url: '/dashboard',
        templateUrl: 'app/dashboard/dashboard.html',
        controller: 'DashboardController',
        controllerAs: 'dashboard'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

